import React from 'react';

const PsychologistTeachers = () => {
  return (
    <React.Fragment>
      <div className="article">
        <article>
          <h5>Сім способів зняти стрес і поліпшити настрій</h5>
          <p>Не секрет, що в наш неспокійний час небезпеку втратити нормальна психологічна рівновага досить велика. Тим більше, якщо робота пов’язана із значними інформаційними перевантаженнями. Для того, щоб не втратити остаточно душевну рівновагу, досвідчені люди пропонують декілька нескладних рад, які, ми упевнені, допоможуть вам з честю вийти з поєдинку із стресом.</p>

          <ul>
            <li><h5>Почніть з того, що зупинитеся хоч би на мить і усвідомте те, що відбувається:</h5></li>
            <p>Поставте собі питання, чи дійсно ваше життя саме така, якій ви б хотіли її бачити? Подумайте над тим, що б ви хотіли в ній зрадити і як цього краще всього досягти. Психологи радять періодично упорядковувати думки, мети, бажання, погоджуючи їх один з одним. Розкладете все по поличках Вашого внутрішнього будинку, викинете непотріб, що накопичився, зробіть генеральне прибирання і гарненько провітріть всі кімнати.</p>

            <li><h5>Купіть, нарешті, собі те, що ви хочете</h5></li>
            <p>Іноді навіть яка-небудь симпатична дрібниця на зразок брилка або веселої листівки може підняти настрій. Надрукуйте свою кращу фотографію, де ви зображені з променистою усмішкою на тлі квітучого саду і поставте в рамку. Постарайтеся, щоб Вас оточували тільки приємні вам речі. Можете без розкаянь совісті відправити на звалище історії стародавній усохлий букет, дарований колись екс-коханим, якщо один його вигляд наганяє на вас сумні спогади.</p>

            <li><h5>Дозвольте собі добре відпочити</h5></li>
            <p>Можна побалувати себе. Приготуйте своє улюблене блюдо, прийміть ванну з ефірними маслами, запаліть в кімнаті ароматичні свічки і включите приємну музику. Створіть атмосферу затишку, в якій Вам би хотілося просто відсторонитися від зовнішнього світу. Займіться любов’ю. Добре допомагають тривалі прогулянки на природі. Насолодіться ароматом соснового лісу, прілого листя і моху, терпким запахом прохолоди вечірнього лугу або берега! Відчуйте тихе дихання вітерця у волоссі, смак яблук в роті, відчуйте життя навколо.</p>

            <li><h5>Приведіть в порядок свій раціон</h5></li>
            <p>Фахівці радять харчуватися приблизно через кожні чотири години і помалу. Як не дивно, тут Вам допоможуть вуглеводи. Невеликі порції багатої вуглеводами їжі сприяють виробленню більшої кількості серотоніна і підняттю настрою.</p>

            <li><h5>Завжди пам’ятаєте про фізичні вправи</h5></li>
            <p>Наприклад, захопіться танцями або займіться плаванням. Вода допомагає тілу відпочити і розслабитися, підвищуючи тонус і настрій. Крім того, можливість нещасного випадку або травми при плаванні мінімальна, на відміну від багатьох інших видів спорту. Прагніть не піддаватися нав’язливій думці пошкодувати себе. Станьте на деякий час трудоголиком, займіться тим, що б допомогло вам відвернутися від нав’язливих думок. Займіться домашніми справами, сходіть в магазин, вигуляйте собаку. Будьте активні!</p>

            <li><h5>Як би це не було важко, навчіться прощати і забувати</h5></li>
            <p>Таким чином Ви позбавитеся роздратування і мстивих відчуттів, які руйнують в першу чергу нас самих. Крім того, сердившись на когось або щось, Ви зберігаєте зв’язок з ним. Відбувається зациклення на минулих образах і засмученнях. Полегшіть себе прощенням, забудьте те, що вже пройшло, і рухайтеся далі!</p>

            <li><h5>Відносьтеся до життя простіше</h5></li>
            <p>Спробуйте хоч іноді пустити справу на самоплив, перестаньте контролювати всіх і вся. У житті завжди є місце подвигу, але потрібен він далеко не завжди. Спробуйте жити одним днем, прагніть зосередитися на тому, що ви робите в дану хвилину. Результат Вас приємно здивує.</p>
            <p>Постарайтеся стати реалістами, не перебільшуючи значення невдач. Завжди пам’ятаєте стародавню східну приказку про те, що якщо 15 днів в місяці темні і сумні, то інші – радісні і щасливі. Станьте оптимістично, навчіться відноситися до подій, що відбуваються в житті, і ситуацій об’єктивніше. Можливо, вони зовсім не такі безнадійні, як вам здається під впливом емоцій. А може, все, що відбулося, і є дорога до кращого? Адже те, що сьогодні здається трагедією, завтра викличе усмішку.</p>
            <p>А головне – завжди пам’ятаєте, що життя наше всього лише маленький промінець світла, що пробивається серед хаосу Всесвіту. Так чи не краще провести її в щасті і гармонії з собою і миром, даруючи тепло і розуміння всім оточуючим?</p>
          </ul>

          <h5>Рекомендації вчителям, щодо підтримки дисципліни в класі</h5> 
          <p>Дисципліна – це умова, засоби й результат виховання, вона потребує зустрічних зусиль. Головною умовою дисциплінованої поведінки учнів є доброзичливі відносини між учителями і дітьми.</p>
          <p>Необхідно враховувати, що дисципліну в класі обумовлюють раніше нагромаджений досвід поведінки учнів, організованість класу, ставлення до навчання, предмета, вчителя, дотримання єдиних вимог до учня всіма вчителями, які працюють у класі, загального порядку умов і традицій даної школи.</p>
          <p>При роботі в класі і для підтримання дисципліни необхідно враховувати наступні правила:</p>
          <p>Поводьтеся так, щоб учні відчували, що ви керуєте навчальним процесом, при цьому стимулюйте їхню активність.</p>
          <p>Не відволікайтеся на незначні порушення, моралізування.</p>
          <p>Дотримуйтеся доброзичливого, мажорного тону, будьте уважні до кожного учня. Надавайте необхідну підтримку, відзначайте успіхи, створюйте життєрадісний оптимістичний настрій у класі.</p>
          <p>Стежте за правильністю постави, проводьте фізкультхвилинки; не допускайте перевтоми учнів.</p>
          <p>Надавайте учням можливість частіше працювати у групах – це може бути стимулом до зайнятості кожного, хорошого настрою і поведінки.</p>
          <p>Створюйте умови, щоб важкі, слабкі діти мали можливість відчути себе лідерами.</p>
          <p>Не принижуйте учнів, не припускайте образ, сарказму. Гнів, дратівливість, імпульсивність не сприяють зміцненню дисципліни й авторитету вчителя.</p>
          <p>Будьте привітними.</p>
          <p>Не виявляйте антипатій до важких учнів, сподіваючись від них поганої поведінки. Діти відчувають скептичне ставлення до них і часто поводяться згідно з вашими очікуваннями.</p>
          <p>Не припускайте появи „любимчиків”, це створює зайві проблеми, ускладнює стосунки в колективі.</p>
          <p>Умійте керувати емоціями, знайте ціну кожному слову, тонові. Не можна вимагати від дитини те, що під силу дорослому, будьте терплячими.</p>
          <p>Не соромтеся виявляти почуття гумору. Можна посміятися і над собою, не вважайте себе досконалістю. Але не можна висміювати учнів. Дітям імпонує веселий, спритний, оптимістичний учитель.</p>
          <p>Підтримуйте контакт з учнями поза уроками, на заняттях  гуртка, позакласних заходах, спільних походах.</p>
          <p>Зміцнюйте зв’язки з батьками, намагайтеся отримати від них підтримку, але не сподівайтеся, що вони вирішать проблему з дисципліною в класі. Виявляйте коректність у взаєминах з батьками. Постійна негативна інформація, скарги псують відносини, викликають недовіру до вчителя, його можливостей, здібностей навчати й виховувати дітей.</p>
        </article>
      </div>
    </React.Fragment>
  );
};

export default PsychologistTeachers;
